	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox:-ms-input-placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox::-ms-input-placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}
#searchBox::placeholder {
	background-color: #142438;
	color: white;
	padding: 5px 0px;
	position: relative;
	top: 3px;
}

.list-group-item:hover {
	background-color: #2d5d8e !important;
}

.list-group-item:hover span:first-child {
	color: white !important;
}

